.Files {
    padding: 5vh 0vw;
}

.user-name {
    font-size: 30px;
    font-weight: 600;
}

.nf-container {
    display: flex;
    justify-content: center;
    align-items: center;
}

.rxn-no-img {
    width: 5.5cm;
    padding-top: 5vh;
}
.notes-container{
    // border: 1px solid green;
    display: flex;
    flex-wrap: wrap;
    column-gap: .5cm;
    row-gap: .5cm;
}

.note{
    width: 7cm;
    min-height: 4cm;
    // border: 1px solid black;
    background-color: rgb(160, 244, 164);
    border-radius: 5px;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;

}
.note-msf{
    padding: .1cm .2cm;
    // background-color: rgb(248, 171, 246);
    // border-radius: 5px;
}
.note-title{
    font-weight: 500;
    border-bottom: thin solid rgb(138, 137, 137);
    
    // border-radius: 5px;
}
.save-file {
    font-size: 14px;
    padding: .17cm .5cm;
    cursor: pointer;
    background-color: rgb(201, 145, 252);
    border: none;
    border-radius: 3px;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;


}
.save-file:hover {
    
    background-color: rgb(197, 136, 253);
   

}

.sub-search {
    color: rgb(96, 95, 95);
}

.saved-line {
    padding-top: 2vh;
    font-size: 23px;
    font-weight: 600;
}

.file-name {
    font-size: 18px;
    cursor: pointer;
    padding-top: .1cm;
    width: fit-content;
}

.file-name:hover {
    text-decoration: underline;
}

@media screen and (max-width:600px) {
    .rxn-no-img {
        width: 4.5cm;
    }

    .user-name {
        font-size: 25px;
    }

    .saved-line {

        font-size: 18px;

    }

    .sub-search {
        font-size: 14px;
    }

    .file-name {
        font-size: 17px;

    }
}

@media screen and (max-width:500px) {
    .rxn-no-img {
        width: 4cm;
    }

    .user-name {
        font-size: 20px;
    }

    .file-name {
        font-size: 17px;

    }
}

@media screen and (max-width:400px) {
    .rxn-no-img {
        width: 3.5cm;
    }
}

@media screen and (max-width:300px) {
    .rxn-no-img {
        width: 3cm;
    }
}